import Router from '_vue_/router';

export default {
    // 
    prev (state , route) {
        state.routeDir = 'back';
        Router.push(route);
    } ,
    // 
    next (state , route) {
        state.routeDir = 'next';
        Router.push(route);
    } ,
    // 
    back (state) {
        if (window.history.length === 0) {
            layer.msg('');
            return ;
        }
        state.routeDir = 'back';
        Router.go(-1);
    } ,
    forward (state) {
        state.routeDir = 'next';
        Router.go(1);
    } ,
    // 
    user (state , {user , login}) {
        state._user_ = user;
        state.login = login;
    } ,

}
